<template>
  <div>
    <router-link :to="{ path: '/shoplist', query: { category: 301 } } ">한식</router-link> |
    <router-link :to="{ path: '/shoplist', query: { category: 302 } }">양식</router-link> |
    <router-link :to="{ path: '/shoplist', query: { category: 303 } }">일식</router-link> |
    <router-link :to="{ path: '/shoplist', query: { category: 304 } }">중식</router-link> |
    <router-link :to="{ path: '/shoplist', query: { category: 305 } }">분식</router-link>
    <h2>{{ cateId }}의 shop list 페이지</h2>
    <!-- fkey에 해당하는 카드 꺼내는 컴포넌트 -->
    <!-- <DJ_C_SCateCard /> -->
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      cateId: null,
    };
  },
  watch: {
    '$route.query.category': {
      immediate: true,
      handler(newVal) {
        this.cateId = newVal;
        this.fetchData(newVal); // URL이 변경될 때마다 데이터를 가져오는 메서드 호출
      },
    },
  },
  methods: {
    async fetchData(categoryId) {
      try {
        const response = await axios.get('/shoplist', {
          params: {
            category: categoryId
          }
        });
        console.log(response.data); // 서버로부터 받은 데이터 출력
      } catch (error) {
        console.error('Error while fetching data:', error);
      }
    },
  },
  mounted() {
    this.fetchData(this.$route.query.category);
  },
};
</script>
